//
// Contents > Modals
// --------------------------------------------------

/** registration **/
.modal {
    .panel_options {
        @include clearfix;
        margin: 10px 0;

        .label_required {
            float: left;
            margin-top: 10px;
        }
        .toggle_panels {
            float: right;
        }
    }
    .toggle_panels {
        min-width: 120px;

        &:before {
            content: "Expand All";
            font-family: inherit;
        }
        &.active {
            box-shadow: none;

            &:before {
                content: "Collapse All";
            }
        }
    }
    .panel {
        box-shadow: none;
    }
    .panel-heading {
        position: relative;
    }
    .toggle_panel {
        position: absolute;
        right: 0;
        top: 50%;
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
    }
}

