.overflow_hidden_height {
    overflow-y: auto;
    height: 600px;
}
.modal .title_box {
    display: table;
    width: 100%;
    padding: 10px 0 30px;

    .title_con,
    .title_con + p {
        display: table-cell;
        vertical-align: middle;
    }
    .title_con + p {
        text-align: right;
    }
}

