// tablist
// --------------------------------------------------
.panel_basic {
    .panel-heading {
        padding: 0;
        border: 0;
        border-radius: 0;
        background-color: $gray-lighter;
        -webkit-box-shadow: none;
        box-shadow: none;

        a:before {
            font-family: FontAwesome;
            content: '\f107';
            float: right;
            -webkit-transition: all 0.3s;
            -o-transition: all 0.3s;
            transition: all 0.3s;
            font-size: 20px;
        }
        &.active a:before {
            -webkit-transform: rotate(180deg);
            -ms-transform: rotate(180deg);
            transform: rotate(180deg);
        }
    }
    .panel-title {
        & > a,
        &> a:active {
            display: block;
            padding: 15px 20px;
            color: #555;
            font-size: 16px;
            font-weight: 600;
            text-transform: uppercase;
            letter-spacing: 1px;
            word-spacing: 3px;
            text-decoration: none;
        }
    }
    .panel-body {
        padding-left: 20px;
        padding-right: 20px;
    }
    [class^=form] .form_ttl {
        margin-left: -20px;
        margin-right: -20px
    }
}

