@charset "utf-8";

$border-tbl-color: #ebebeb;
$bg-tbl: #f9f9f9; //#efeff3;

//
// Libraries > Bootstrap-select
// --------------------------------------------------

.bootstrap-select.form-control {
    height: auto;
}

//
// Libraries > bootstrap-datepicker
// --------------------------------------------------

.input-daterange {
    @include clearfix;

    > * {
        float: left;
    }
    .input-group {
        width: 47.5%;
    }
    .dash {
        width: 5%;
        line-height: 2.45;
        text-align: center;
    }
    .input-group-addon {
        border-width: 1px 1px 1px 0;
        background-color: #fff;
    }
}

//
// Libraries > iCheck
// --------------------------------------------------

.icheckbox_minimal {
    background-color: #fff;
}
.checkbox-inline, .radio-inline {
    .icheckbox_minimal, .iradio_minimal {
        margin-left: -20px;
    }
}

//
// Libraries > bootstrap
// --------------------------------------------------

/** tab **/
.tab-content {
    padding: 30px 0;
}

/** form **/
.control-label {
    font-size: 15px;
    font-weight: normal;
    color: #414042;
}
.form-control {
    border-color: #bbb;
    color: #414042;
}
.form-horizontal {
    .form-group {
        margin-left: -5px;
        margin-right: -5px;
        margin-bottom: 10px;

        [class^=col-], label.ttl {
            padding-left: 5px;
            padding-right: 5px;
        }
    }
    .control-label {
        text-align: left;
    }
}
form, .form {
    .row {
        [class^=col-] {
            //  2018-02-06 MAC OS Chrome - .toolbar 에서 이상 
            // padding-left: 5px;
            // padding-right: 5px;
        }
    }
}
.input-group {
    input[type=text].form-control + .input-group-btn .btn {
        line-height: 20px;
    }
    .input-group-btn .btn {
        border-color: #bbb;
    }
}

/** Pagination **/
.pagination > li > a,
.pagination > li > span {
    margin: 0 5px;
    border-color: transparent;
    border-radius: 50%;
}

.pagination > li > a:hover,
.pagination > li > a:focus,
.pagination > li > span:hover,
.pagination > li > span:focus {
    border-color: transparent;
    background-color: #fff;
    color: #000;
}

.pagination > li.active > a,
.pagination > li.active > span {
    border-color: transparent;
    background-color: $gdkm-color-1st;
    color: #fff;
}

.pagination > li:first-child > a,
.pagination > li:first-child > span,
.pagination > li:last-child > a,
.pagination > li:last-child > span {
    border: 0;
    color: #ddd;
}

/** panels **/
.panel-group {
    margin-bottom: 10px;
}

//
// Components > Buttons
// --------------------------------------------------

/**  buttons **/
.btn_dark {
    color: #e9edef;
    background-color: #363636;
    border-color: darken(#363636, 10%);
}
.btn_dark:hover, .btn_dark:focus, .btn_dark:active, .btn_dark.active, .open .dropdown-toggle.btn_dark {
    color: #fff;
    background-color: darken(#363636, 5%);
    border-color: darken(#363636, 15%);
    z-index: inherit !important;
}
.btn_brand {
    background-color: $brand-secondary;
    border-color: darken($brand-secondary, 10%);
}
.btn_brand:hover, .btn_brand:focus, .btn_brand:active, .btn_brand.active, .open .dropdown-toggle.btn_brand {
    background-color: darken($brand-secondary, 5%);
    border-color: darken($brand-secondary, 15%);
}
.btn_secondary {
    color: #fff;
    background-color: $gray;
    border-color: none;
}

.btn_secondary:hover, .btn_secondary:focus, .btn_secondary:active, .btn_secondary.active, .open .dropdown-toggle.btn_secondary {
    color: #fff;
    background-color: darken($gray, 9%);
    border-color: none;
}

//
// Components > Tables
// --------------------------------------------------

/** layout table **/
.tbl {
    display: table;
    width: 100%;
}

.col_tbl {
    display: table-cell;
}

//
// Components > Panels
// --------------------------------------------------

.panel_g {}

.panel_h {
    color: #333;
    background-color: #fff;
    border-color: #e4e5e7;
    padding: 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    a {
        display: block;
        padding: 10px 15px;

        &:after {
            content: '';
            position: relative;
            top: 1px;
            display: inline-block;
            font-family: 'Glyphicons Halflings';
            font-style: normal;
            font-weight: 400;
            line-height: 1;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            float: right;
            transition: transform .25s linear;
            -webkit-transition: -webkit-transform .25s linear;
        }
    }
    a[aria-expanded='true'] {
        background-color: #eee;

        &:after {
            content: '\2212';
            -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
        }
    }
    a[aria-expanded='false']:after {
        content: '\002b';
        -webkit-transform: rotate(90deg);
        transform: rotate(90deg);
    }
}

.toggle_panels {
    &:before {
        //content: "Expand All";
        content: '\f107';
        font-family: FontAwesome;
        font-weight: normal;
    }
    &.active {
        box-shadow: none;

        &:before {
            //content: "Collapse All";
            content: '\f106';
        }
    }
}

.toggle_panel {
    background-color: transparent;

    &:before {
        content: '\f107';
        font-family: FontAwesome;
        font-weight: normal;
    }
    &.active {
        box-shadow: none;

        &:before {
            content: '\f106';
        }
    }
}

