//
// Components > Modals
// --------------------------------------------------

.modal-header,
.modal-footer {
    margin: 0 20px;
    padding-left: 0;
    padding-right: 0;
}

.modal-header {
    position: relative;
    padding-top: 25px;
    padding-bottom: 10px;

    .close {
        position: absolute;
        top: 50%;
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);
        right: 0;
        width: 40px;
        height: 40px;
        line-height: 40px;
        margin: 0;
        border: 1px solid #333;
        border-radius: 50%;
        opacity: 0.5;

        &:hover {
            opacity: 0.7;
        }
    }
}

.modal-footer {
    text-align: center;
}

.modal-title {
    margin: 0;
    letter-spacing: -1px;
    word-wrap: break-word;
    word-break: break-all;
}

