//
// contents > Registration
// --------------------------------------------------

/** search-form **/
.search_form_tbl {
    position: relative;
    border: 1px solid $border-tbl-color;
    padding: 15px 20px;
    background-color: $bg-tbl;
    @extend .tbl;

    & > .col_tbl:nth-child(2) {
        // 2018-02-07 IE - 버튼 높이 안잡이는 현상
        // position: relative;
        width: 120px;
        vertical-align: middle;

        .btn {
            @include position(absolute, 0, 20px, 0, inherit);
            width: 110px;
            margin: 15px 0;
        }
    }

    .form-group:last-child {
        margin-bottom: 0;
    }
}

/** toolbar **/
.search_form_tbl + .toolbar {
    margin-top: 40px;
    margin-bottom: 10px;
    @include clearfix;

    > [class^=col-]:last-child {
        text-align: right;
    }
}

/** list table **/
.list_tbl {
    width: 100%;
    border-top: 2px solid #ffad00;
    border-bottom: 1px solid #444444;

    th, td {
        padding: 6px 8px;
        line-height: 1.428571;
        font-size: 14px;
        color: #444;
    }
    & > thead {
        & > tr > th {
            vertical-align: middle;
            font-size: 15px;
            text-align: center;
        }
        & > tr:last-child > th {
            border-bottom: 1px solid #444444;
        }
    }
    & > tbody {
        & > tr > th,
        & > tr > td {
            border-bottom: 1px solid #ccc;
            vertical-align: middle;
        }
    }
    .subj {
        @include text-overflow;
    }
    .panel_h {
        background-color: #fff;
        -webkit-transition: background-color 500ms;
        -moz-transition: background-color 500ms;
        -o-transition: background-color 500ms;
        transition: background-color 500ms;

        a {
            display: initial;
            padding: initial;
        }
    }
    .panel_h.active {
        background-color: rgba($brand-secondary, .04); //#fcfaf4; //#efeff3
    }
    .panel-collapse {
        & > td {
            padding: 5px 0 10px;
        }
    }
    .table {
        margin: 0;

        th, td {
            border-color: #eee;
            padding: 3px 15px;
            font-size: 13px;
            text-align: left;
        }
        tbody {
            & > tr:first-child > th,
            & > tr:first-child > td {
                border-top-color: transparent;
            }

            & > tr > th {
                width: 25%;
            }
        }
    }
}

/** Registration forms **/
.label_required {
    color: red;
}
.required {
    &:after {
        content: ' *';
        vertical-align: bottom;
        font-size: 85%;
        color: red;
    }
}
[class^=form] {
    .form_ttl {
        margin: 10px -15px;
        padding: 4px 20px;
        font-size: 80%;
        color: $gray-light;
        background-color: rgba($brand-primary, .08);
    }
    .help-block {
        font-size: 13px;
    }
    .tbl {
        width: 100%;

        th, td {
            padding: 5px 0;
            vertical-align: middle;
            font-weight: normal;
            color: #414042;
        }
        thead {
            th {
                font-size: 15px;
                font-weight: 600;
                text-align: center;
            }
        }
    }
}
