@charset "utf-8";

//@import 'NanumGothic';
@import 'mixins';
@import 'variables';
@import 'bootstrap';
@import 'reset';
@import 'helper/helper';

/* ================== common layout ================== */

html, body {
    height: 100%;
    min-height: 100%;
}
.wrap {
    position: relative;
    min-width: 1200px;
    min-height: 100%;
    box-sizing: content-box;
}
main {
    position: relative;
    width: 100%;
    max-width: 100%;
    padding: 0 0 80px;
    //text-shadow: 1px 1px 1px rgba(255,255,255,0.8);
    @include clearfix;
}
.container {
    position: relative;
    width: 1200px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
    @include clearfix;
}
.w {
    position: relative;
}
caption {
    overflow: hidden;
    width: 100%;
    height: 1px;
    padding: 0;
    line-height: 0;
    margin: -1px 0 0 !important;
    font-size: 0;
    text-indent: -9999px;
}


//
// common components
// skip-to-content
// --------------------------------------------------

#skip_to_content {
    display: block;
    padding: 1em;
    color: #000;
    outline: 0;
    box-shadow: rgba(0, 0, 0, 0.09) 0px 1px 0px 0px;
    @extend .sr-only;
    @extend .sr-only-focusable;

    .skiplink-text {
        padding: .5em;
        outline: 1px dotted;
    }
}

//
// components > Navigation
// --------------------------------------------------

/** menus **/
.nav_h {
    margin-bottom: 0;
    padding-left: 0;
    @include clearfix;

    > li {
        display: block;
        float: left;
        position: relative;
    }
}

/** links **/
$nav-link: #777;
$nav-link-hover: #c18300;
$nav-link-border: #d99300;

.nav_link {
    color: $nav-link;

    &:hover, &:focus {
        color: $nav-link-hover;
        text-decoration: none;
    }
}


//
// common components > Forms
// --------------------------------------------------

/** forms **/
.form_i {
    .form_g {
        display: inline-block;
        //vertical-align: middle;
    }
}
.form_h {
    .form_g {
        margin-right: -15px;
        margin-left: -15px;
        margin-bottom: 15px;
        @include clearfix;

        & > * {
            float: left;
        }
    }
}

/* ================== common header ================== */

$navbar-1st-height: 42px;
$navbar-2nd-height: 120px;
$navbar-3rd-height: 44px;

$gdkm-color-1st: #ffad00;
//$gdkm-color-2nd: #ffd681;
//$gdkm-color-3rd: #ffd681;

header {
    z-index: 1000;
    width: 100%;

    .w {
        height: 120px;
        background-color:  $gdkm-color-1st; //#f8a532;

        .container {
            padding: 40px 15px 0 550px;
            text-align: right;
            @include clearfix;
        }

        .brand {
            position: absolute;
            top: 60px;
            left: 15px;
        }
    }

    .navbar_h:first-child {
        height: $navbar-1st-height;
        overflow: hidden;
        will-change: transform;
        transition: transform 200ms linear;
        border-bottom: 1px solid #d99300;
        background-color: #fff;
        font-size: 14px;
    }

    .navbar_h:last-child {
        height: $navbar-3rd-height;
        -webkit-transition: height .5s ease-in-out;
        transition: height .5s ease-in-out;
        background-color: #ffd681;
    }
}


//
// common components
// top-shorcuts(links)
// --------------------------------------------------

#shorcut_top {
    float: right;

    .nav_link {
        display: block;
        line-height: $navbar-1st-height;
        padding: 0 20px;
        border-right: 1px solid $nav-link-border;
        font-size: 13px;
    }

    .nav_item:first-child .nav_link {
        border-left: 1px solid $nav-link-border;
    }
}


//
// common components
// multi-search-form
// --------------------------------------------------

#search_top {
    margin-left: -2px;
    margin-right: -2px;
    @include clearfix;

    .form_g {
        float: left;
        width: 33.33%;
        padding-left: 2px;
        padding-right: 2px;
        text-align: left;

        label {
            color: #fff;
            font-size: 13px;
            font-weight: 300;
        }
    }

    .btn {
        padding-top: 6.5px;
        padding-bottom: 6.5px;
    }
}


//
// common components
// global-navigation-bar (main-menu)
// --------------------------------------------------

.dropdown_menu {
    height: 0;
    opacity: 0;
    overflow: hidden;
    visibility: hidden;
    list-style-type: none;
    margin: 0;
    padding: 0;
    background: #fff;
    border-radius: 3px;
    box-shadow: 0 2px 18px rgba(0,0,0,0.2);
    box-sizing: border-box;
    font-size: 85%;
    left: 50%;
    margin-left: -100px;
    position: absolute;
    text-align: center;
    //top: -5%;
    top: 4.375em;
    width: 200px;
    z-index: 1;
}
.dropdown_menu_active {
    height: auto;
    opacity: 1;
    overflow: visible;
    visibility: visible;
    top: 100%;
    transition: opacity 0.25s ease-in-out, top 0.25s ease-in-out;
}
.dropdown_item {
    border-bottom: 1px solid #e2e8e9;
    display: block;
    margin: 0;
    padding: 0;
    color: rgba(0,0,0,.87);
    outline-color: #bdbdbd;
    font-size: 14px;
    font-weight: 400;
    transition: opacity .2s cubic-bezier(.4,0,.2,1);
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    //&:focus, &:hover {
    //    outline: none;
    //    background-color: #eee !important;
    //}
}
.dropdown_item_link {
    display: block;
    padding: 0.625em 1.25em;
    color: #393d40;
    cursor: pointer;
}

#gnb {
    margin-left: -20px;
    z-index: 1;

    .nav_item:hover, .nav_item:focus {
        .nav_link {
            color: darken($nav-link, 20%);
        }
    }
    .nav_link {
        padding: 0 20px;
        line-height: $navbar-3rd-height;
        font-size: 16px;
        white-space: nowrap;
        -webkit-transition: all .25s ease;
        -moz-transition: all .25s ease;
        -ms-transition: all .25s ease;
        -o-transition: all .25s ease;
        transition: all .25s ease;
    }
    .dropdown_menu {
        z-index: 1;
        min-width: 100px;
        padding: 5px 0;
        border-radius: 0 0 3px 3px;
        background-color: #ffd681;
        text-align: left;
        box-shadow: 0 1px 8px rgba(0,0,0,0.15);
    }
    .dropdown_item {
        border-color: transparent;
    }
    .dropdown_item_link {
        padding: 10px 20px;
        color: $nav-link;
        font-size: 16px;
        text-decoration: none;
        white-space: nowrap;

        &:hover, &:focus {
            background: darken(#ffd681, 5%);
            color: darken($nav-link, 20%);;
        }
    }
    /* slide animation */
    & > .nav_item:hover, & > .nav_item:focus {
        .dropdown_menu {
            @extend .dropdown_menu_active;
        }
    }
    /* slide up and down bullet */
    & > .nav_item.has_child {
        & > .nav_link:after {
            content: "\f107";
            font-family: FontAwesome;
            font-weight: normal;
            line-height: $navbar-1st-height;
            margin-left: 10px;
        }
    }
}


/* ================== common footer ================== */

footer {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 80px;
    font-size: 12px;
    color: #777;

    & > .container {
        @include clearfix;
    }
    .logo_w, .txt_w {
        float: right;
        margin-top: 15px;
    }
    .txt_w {
        margin-right: 25px;
        text-align: right;
    }
}

//
// common components
// scroll-to-top
// --------------------------------------------------

.scroll-to-top {
    position: fixed;
    bottom: 15px;
    right: 15px;
    opacity: 0;
    transition: all .2s ease-in-out;

    button {
        width: 50px;
        height: 50px;
        padding: 5px 5px 0;
        border-color: transparent;
        border-radius: 50%;
        background-color: white;
        color: #787878;
        box-shadow: $shadow;
        outline: none;
    }
}
.scroll-to-top.show {
    opacity: 1;
    transition: all .2s ease-in-out;
}


//
// common components
// breadcrumb
// --------------------------------------------------

.breadcrumb {
    margin: 35px -15px 5px 0;
    background-color: #fff;
    font-weight: 300;

    a {
        color: #777;
    }

    .active {
        color: darken(#777, 15%);
    }

    > li + li:before {
        content: "\f105";
        padding: 0 6px 0 2px;
        font-family: FontAwesome;
        font-weight: normal;
    }
}


//
// common components
// title
// --------------------------------------------------

/* page title */
.content_top {
    position: relative;
    margin-bottom: 20px;
    border-bottom: 2px solid #ffad00;
    @include clearfix;

    & > *:nth-child(odd) {
        float: left;
    }

    & > *:nth-child(even) {
        float: right;
    }
}
.title_page {
    margin: 30px 0 5px -20px;
    //padding: 8px 0;
    font-size: 25px;
    font-weight: 400;

    a {
        display: inline-block;
        position: relative;
        color: #abaaaa;
        // padding: 8px 0;
        padding-left: 15px;
        text-decoration: none;
    }
    a:first-child {
      // padding: 8px 20px;
      padding: 0 0 0 20px;
    }

    a.active {
        color: #666;
    }

    // a.active ~ a:before {
    //     content: '';
    //     display: inline-block;
    //     width: 2px;
    //     height: 18px;
    //     margin: 0 20px 0 -20px;
    //     background-color: #ffad00;
    // }
    a:after {
        content: '';
        display: inline-block;
        width: 2px;
        height: 18px;
        // margin: 0 20px 0 -20px;
        margin-left: 20px;
        background-color: #ffad00;
    }
    a:last-child:after {
      content: initial;
      display: initial;
      width: inherit;
      height: inherit;
    }
}

/* content title */
.title_con {
    margin: 30px 0 15px;
    padding-left: 30px;
    background: url(../images/con-bul-01.png) 0 3px no-repeat;
    font-size: 20px;
    font-weight: 400;
    line-height: 1;
    color: #666;
}

/* ================== contents ================== */

@import 'contents';
