// override
.tree_wrap {
  background-color: #f9f9f9;
  border: 1px solid #ebebeb;
  padding: 20px;

  .jstree-anchor {
    color: $gray;
  }
  i {
    color: $gray-light;
  }
  .jstree-default .jstree-wholerow-clicked {
    background: rgba($brand-secondary, .3) !important;
  }
  .jstree-default .jstree-wholerow-hovered {
    background: rgba($brand-secondary, .1) !important;
  }
}

[id^=keyword_opt] {
  display: none;
  margin-bottom: 0 !important;
}
.list_tbl.panel_g {
  button[data-toggle="toggle"] {
    background: transparent;
  }
}
