/*
override
*/
*.nav-tabs > li.active > a,
.nav-tabs > li.active > a:hover,
.nav-tabs > li.active > a:focus {
  color: $brand-secondary;
  font-weight: 600;
}

.lnb_wrap {
  border: 1px solid $input-border;

  .lnb_header {
    text-align: center;

    h3 {
      color: #ffffff;
      font-size: 20px;
      font-weight: 600;
      padding: 28px 0;
      margin: 0;
      background-color: $brand-secondary;
    }
  }
  .lnb_body {
    ul {
      margin-bottom: 0;
    }

    ul li.active {
      &>a {
        color: $brand-secondary;
        font-weight: 700;
      }
    }

    ul li a {
      display: block;
      font-size: 15px;
      text-decoration: none;
      border-bottom: 1px solid $input-border;
      padding: 15px 10px;

      &:hover {
        color: $brand-secondary;
      }
    }
    ul li:last-child a {
      border-bottom: 0;
    }

  }
}
