@charset "utf-8";

.option1_j, .option2_j {
    display: none;
}

/* ================== table ================== */
.data_tbl {
    width: 100%;
    border-collapse: collapse;
    border-top: 2px solid $brand-secondary;

    caption {
        height: 0;
        font-size: 0;
        padding: 0;
    }

    td, th {
        border: 1px solid $input-border;
        padding: 12px 10px;
    }
    th {
        text-align: center;
        background-color: $bg-tbl;
    }
    thead th {
        border-bottom: 1px solid #444;
    }
}

/* ================== override tab ================== */
.tab_level2 {
    text-align: center;
    border-bottom: 0;

    & + .tab-content {
        padding: 40px 0 20px !important;
    }

    li {
        float: inherit;
        display: inline-block;
        margin: 0 0.5px;
    }
    li a {
        display: block;
        font-size: 20px;
        color: #999;
        letter-spacing: -.75px;
        width: 100px;
        text-align: center;
        padding-bottom: 15px;
        position: relative;
    }
    li.active a:before,
    li:hover a:before {
        background: $brand-secondary;
    }
    li.active a,
    li:hover a {
        color: $brand-secondary;
    }
    li a:before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        height: 4px;
        width: 100%;
        background: $input-border;
    }
}

.tab_level3 {
    border-bottom: 0;
    li a {
        font-weight: 700;
        padding: 6px 20px;
    }
    & + .tab-content {
        padding: 0 !important;
    }
}

.nav-tabs.tab_level3 > li.active > a,
.nav-tabs.tab_level3 > li.active > a:focus,
.nav-tabs.tab_level3 > li.active > a:hover {
    color: #ffffff;
    border-radius: 40px;
    background-color: $brand-secondary;
}

.nav-tabs.tab_level2 > li.active > a,
.nav-tabs.tab_level2 > li.active > a:focus,
.nav-tabs.tab_level2 > li.active > a:hover {
    color: $brand-secondary;
}

.nav-tabs.tab_level3 > li > a,
.nav-tabs.tab_level3 > li > a:focus,
.nav-tabs.tab_level3 > li > a:hover,
.nav-tabs.tab_level2 > li > a,
.nav-tabs.tab_level2 > li > a:focus,
.nav-tabs.tab_level2 > li > a:hover,
.nav-tabs.tab_level3 > li.active > a,
.nav-tabs.tab_level3 > li.active > a:focus,
.nav-tabs.tab_level3 > li.active > a:hover,
.nav-tabs.tab_level2 > li.active > a,
.nav-tabs.tab_level2 > li.active > a:focus,
.nav-tabs.tab_level2 > li.active > a:hover {
    border: 0;
}

.nav-tabs.tab_level2 > li > a:hover,
.nav-tabs.tab_level2 > li > a:focus,
.nav-tabs.tab_level3 > li > a:hover,
.nav-tabs.tab_level3 > li > a:focus {
    border: 0;
    background-color: inherit;
}

img {
    max-width: 100%;
    width: 100%;
}

.addon_custom .input-group-addon {
    max-width: 33px;
    min-width: 33px;
    height: 33px;
    padding: 4px 5px;
    line-height: 1.42857143;
    text-shadow: 0 1px 0 #fff;
    border-width: 1px 0;
    margin-left: -5px;
    margin-right: -5px;
}

.icheck_custom {
